export default {
 data() {
  return {
   disabled: false,
   time: 60,
   btntxt: "发送验证码",
  };
 },
 methods: {
  sendcode() {
   this.time = 60;
   this.timer();
  },
  //发送手机验证码倒计时
  timer() {
   if (this.time > 0) {
    this.disabled = true;
    this.time--;
    this.btntxt = this.time + "秒";
    setTimeout(this.timer, 1000);
   } else {
    this.time = 0;
    this.btntxt = "发送验证码";
    this.disabled = false;
   }
  },
 }
}
